<template lang="html">
  <div>
    <br>
    <br>
    <v-progress-circular
          v-bind:size="100"
          v-bind:width="15"
          v-bind:rotate="-90"
          v-bind:value="value"
          class="primary--text"
        >
          {{ value }}
    </v-progress-circular>



  </div>
</template>

<script>
  export default {
    data () {
      return {
        interval: {},
        value: 0,
        displayimg: false
      }
    },
    beforeDestroy () {
      clearInterval(this.interval)
    },
    mounted () {
      this.displayimg = false
      this.interval = setInterval(() => {
        if (this.value === 100) {
          this.displayimg = true
          return (this.value = 0)
        }
        this.value += 10
      }, 1000)
    }
  }
</script>


<style lang="stylus" scoped>
  div
    text-align: center

  .progress-circular
    margin: 1rem
</style>

<style lang="css">
</style>



