<template>
  <v-layout row>
    <v-flex xs12 sm6 offset-sm3>
      <v-card>
        <v-card-media
          :src='image'
          height="200px"
        >
        </v-card-media>
        <v-card-title primary-title>
          <div>
            <div class="headline">
              <p>Bravo! Votre photos a bien été enregister, conserver son <v-chip class="green white--text">numéro</v-chip> pour la retrouver sur le site</p>
              <v-chip class="green white--text">
                <v-avatar class="green darken-4">N°</v-avatar>
                {{ number }}
              </v-chip>
            </div>
            <span class="grey--text">Déjà 9,999 K de photos réaliser depuis ce jours</span>
          </div>
        </v-card-title>
        <v-card-actions>
          <v-btn flat>Share</v-btn>
          <v-btn flat class="purple--text">Explore</v-btn>
          <v-spacer></v-spacer>
          <v-btn icon @click.native="show = !show">
            <v-icon>{{ show ? 'keyboard_arrow_down' : 'keyboard_arrow_up' }}</v-icon>
          </v-btn>
        </v-card-actions>
        <v-slide-y-transition>
          <v-card-text v-show="show">
            Retrouvez toutes les photos sur le site <a class="linkToSite">www.hurlomaton.com</a>
          </v-card-text>
        </v-slide-y-transition>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
  export default {
    props: ['image', 'number'],
    data: () => ({
      show: true
    })
  }
</script>


<style lang="css">
.linkToSite{
  text-decoration: underline #63D1F4;
  color:#63D1F4;
}
</style>



