  <template>
  <v-app dark standalone>

      <main>
        <div>

          <v-stepper v-model="e1" dark class="mt-3">
            <v-stepper-header>
              <v-stepper-step step="1" :complete="e1 > 1" ><p class='stepName'>Hurler</p></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step step="2" :complete="e1 > 2" ><p class='stepName'>Attendre</p></v-stepper-step>
              <v-divider></v-divider>
              <v-stepper-step step="3" ><p class='stepName'>Visionez</p></v-stepper-step>
            </v-stepper-header>

            <v-stepper-content step="1">

              <page-a></page-a>

              <v-btn primary @click.native="e1 = 2" id="btnNext2" v-show='false'><p class="btnNextStep">Continue</p></v-btn>
            </v-stepper-content>

            <v-stepper-content step="2">

              <page-b></page-b>

              <v-btn primary @click.native="e1 = 3" id="btnNext3" v-show='false'><p class="btnNextStep">Continue</p></v-btn>
            </v-stepper-content>
            <v-stepper-content step="3">

              <page-c :image='lastimage' :number='lastnumber'></page-c>

              <v-btn primary @click.native="e1 = 1" id="btnNext1" v-show='false'><p class="btnNextStep">Continue</p></v-btn>
            </v-stepper-content>
          </v-stepper>

        </div>

        <page-joke :joke='randomjoke'></page-joke>

      </main>



  </v-app>
</template>

<script>
import axios from 'axios'
import PageA from './components/PageA.vue'
import PageB from './components/PageB.vue'
import PageC from './components/PageC.vue'
import PageJoke from './components/PageJoke.vue'

export default {
  components: { PageA, PageB, PageC, PageJoke },
  data () {
    return {
      loaded: true,
      state: true,
      step: 1,
      e1: 0,
      joke: false,
      jokes: ['Vous etes naz', 'il faut hurler et non pas miauler', 'On a rien entendu', 'plus fort'],
      randomjoke: 'koi',
      lastimage: '',
      lastnumber: ''
    }
  },
  mounted () {
    console.log('mounted')
    let that = this
    setInterval(function () {
      that.fetchstep()
    }, 5000)
  },
  methods: {
    switchstep: function () {
      if (this.step < 4) { this.step += 1 } else { this.step = 0 }
    },
    setstep: function (step) {
      this.step = step
    },
    fetchstep: function () {
      axios.get('http://localhost:3000/step')
        .then((response) => {
          console.log(response)
          console.log(typeof response.data.step)
          var newStep = parseInt(response.data.step)

          if (newStep <= 4 && newStep >= 0) {
            if (newStep !== this.step) {
              if (newStep === 3) {
                this.lastimage = response.data.image
                this.lastnumber = response.data.number
              }
              this.step = newStep
              this.randomjoke = this.jokes[ Math.floor(Math.random() * this.jokes.length) + 0 ]
              this.handlestep(newStep)
            }
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    handlestep: function (step) {
      document.getElementById('btnNext' + step).click()
    }
  }
}
</script>

<style lang="stylus">
  @import './stylus/main'
</style>

<style>
body {
  background: #303030;
  padding:30px;
}
  #example-8 .card {
    background: #424242
  }
.stepName{
  color:white !important;
    margin-top:12px;
  }
  .btnNextStep{
    color:white !important;
  }
  .btn__content{
    display:block;
    margin-top: 8px;
  }
  .material-icons icon{
    display:none;

  }
</style>



